<template>
    <div class='goods'>
    	<div class="goods-menu" ref="menuWrapper">
    		<ul>
    			<li @click='selectMenu(index,$event)' v-for="(v,index) in goods" class="menu-list" :class="{'current':index===currentIndex}">
    			    <span class="text">
                        <span v-show="v.type>0" class="icon" :class="iconClassMap[v.type]"></span>
					    {{v.name}}
					</span>	
    			</li>
    		</ul>
    	</div>
    	<div class="goods-foods" ref="foodsWrapper">
        <div>
            <ul  v-for="good in goods" class="food-list food-list-hook">
                <h1 class="title">{{good.name}}</h1>
                <li v-for="(food,index) in good.foods" @click='showFood(food,$event)'>
                    <div class="food-detail">
                        <div class="icon">
                            <img :src="food.icon" alt="">
                        </div>
                        <div class="content">
                            <h2 class="tittle-right">{{food.name}}</h2>
                            <p class="description">{{food.description}}</p> 
                            <div class="sell-info">
                                <span class="sellCount">{{food.sellCount}}</span>
                                <span class="rating">{{food.rating}}%</span>
                            </div>
                            <div class="price">
                                <span class="newPrice">{{food.price}}</span>
                            </div> 
                        </div>
                        <div class="choose"> 
                            <carcount :food="food"></carcount>   
                        </div>
                    </div>
                </li>   
            </ul>
        </div>   
    	</div> 
        <foodDetail v-if="foodshow"></foodDetail>
        <v-footer></v-footer> 
    </div>		
</template>

<script>
    import {mapGetters} from 'vuex'
    import foodDetail from '../detail/foodDetail.vue'
    import Footer from '../footer/footer.vue'
    import carcount from '../carCount/carCount.vue'
    export default{
    data(){
        return{
            listHeight:[],
            scrollY:0
        }
    },
    created(){
        this.$store.dispatch('getGoods');
        this.iconClassMap = ['decrease','discount','special','invoice','guarantee'];
        this.$nextTick(() => {
            setTimeout(() => {
                this._initMenu();
                this._initFoods();
                this._calcHeight();
            },100)
        })  
    }, 
    computed:{
    	...mapGetters([
            'goods',
            'countShow',
            'countInit',
            'foodshow'
    		]),
        currentIndex(){
            for(let i=0;i<this.listHeight.length;i++){
                let height1 = this.listHeight[i];
                let height2 = this.listHeight[i+1];
                if(this.scrollY>=height1 && this.scrollY < height2){
                    return i;
                }  
            }
            return 0;
        }
    },
    methods:{
        showFood(f,e){
            if(!e._constructed){
                return;
            }
            this.$store.dispatch('showfood',f);
        },
        selectMenu(key,e){
            if(!e._constructed){
                return;
            }
            let foodList = this.$refs.foodsWrapper.getElementsByClassName('food-list-hook');
            let el=foodList[key];
            this.foodBS.scrollToElement(el,300);
        },
        _calcHeight(){
            let foodList = this.$refs.foodsWrapper.getElementsByClassName('food-list-hook');
            let height = 0;
            this.listHeight.push(height);
            for(let i=0;i<foodList.length;i++){
                height+=foodList[i].clientHeight;
                this.listHeight.push(height);
            }
        },
        _initMenu(){
            this.menuBS = new BScroll(this.$refs.menuWrapper,{click:true});
        },
        _initFoods(){
            this.foodBS = new BScroll(this.$refs.foodsWrapper,{
                click:true,
                probeType:3
            });
            this.foodBS.on('scroll',(pos) =>{
                this.scrollY = Math.abs(Math.round(pos.y));
                //console.log(this.scrollY);
            })

        }
    },
    components:{
        foodDetail,
        'v-footer':Footer,
        carcount
    }
    
    }	
</script>

<style lang='less' scoped>
    .goods{
    	position:absolute;
    	top:3.48rem;
    	bottom:0.92rem;
    	width:100%;
    	display:flex;
    	.goods-menu{
    		flex:0 0 1.6rem;
    		width: 1.6rem;
    		font-size:0.24rem;
    		color:rgb(77,85,93);
    		font-weight: 200;
            overflow:hidden;
    		li{
    		    height:1.08rem;
    		    padding: 0 .24rem;
                background-color: #f3f5f7;
                display:table;
                &.current{
                    background:white;
                }
    		    .text{
    		    	text-align: center;
                    vertical-align:middle;
                    display:table-cell;
                    width:1.12rem;
                    border-bottom: 1px solid rgba(7,17,27,0.1);
                    .icon{
                        display:inline-block;
                        width:0.28rem;
                        height:0.28rem;
                        background-size: 0.28rem 0.28rem;
                        &.decrease{
                            background-image: url('../../../static/imgs/decrease.png');
                            vertical-align:middle;

                        }
                        &.discount{
                            background-image: url('../../../static/imgs/discount.png');
                            vertical-align:middle;
                        }
                        &.special{
                            background-image: url('../../../static/imgs/special.png');
                            vertical-align:middle;
                        }
                        &.invoice{
                            background-image: url('../../../static/imgs/invoice.png');
                            vertical-align:middle;
                        }
                        &.guarantee{
                            background-image: url('../../../static/imgs/guarantee.png');
                            vertical-align:middle;
                        }
                    }
    		    }
    		}
    	}
    	.goods-foods{
    		flex:1;
            width:100%;
            overflow:hidden;
            ul{
                margin-bottom: 0.36rem;
                .title{
                    display:block;
                    font-size:0.24rem;
                    color:rgb(147,153,159);
                    width:100%;
                    height:0.52rem;
                    line-height:0.52rem;
                    background-color:#f3f5f7;
                    border-left:0.05rem solid #d9dde1;
                    padding-left:0.28rem;
                }
                .food-detail{
                    padding-top:0.36rem;
                    overflow: hidden;
                    padding-left:0.36rem;
                    position:relative;
                    .icon{
                        float:left;
                        width:1.28rem;
                        height:1.28rem;
                        img{
                            border-radius: 0.04rem;
                            width:1.28rem;
                            height:1.28rem;
                        }
                    }
                    .content{
                        float:left;width:68%; 
                        padding-left:0.2rem;
                        .tittle-right{
                            font-size:0.28rem;
                            color:rgb(7,17,27); 
                            line-height:0.28rem; 
                            margin-bottom: 0.16rem;  
                        } 
                        .sell-info,.description{

                            font-size:0.2rem;
                            line-height:0.3rem;
                            color:rgb(147,153,159);
                            margin-bottom: 0.16rem;

                        }
                        .price{
                            .newPrice{
                                font-size:0.28rem;
                                color:red;
                                font-weight:700;   
                            }
                        }     
                    }
                    .choose{
                        position:absolute;
                        right:0.36rem;
                        bottom:0.1rem; 
                    }
                } 
            }
            
    	}
    }	
</style>


