<template>
    <div class="detail" v-show="detailshow">
        <div class="content">
        	<h1 class="title">{{seller.name}}</h1>
            <div class="stars">
            <star :size="48" :score="seller.score"></star>
            </div>
        	<div class="discounts">
        	    <div class='line'></div>
        	    <div class="message"></div>
        	    <div class='line'></div>	
        	</div>
        	<div class="discounts-detail">
        	    <ul v-if="seller.supports" class="supports">
        	    	<li class="support-item" v-for="item in seller.supports">
        	    		<span class="icon" :class="iconClassMap[item.type]"></span>
        	    		<span class="text">{{item.description}}</span>
        	    	</li>
        	    </ul>	
        	</div>
            <div class="discounts sell-message">
                <div class='line'></div>
                <div class="message"></div>
                <div class='line'></div>    
            </div>
            <div class="sell-detail">
                {{seller.bulletin}}   
            </div>
            <div class="detail-close">
                <i class="el-icon-close" @click='close'></i>   
            </div>
        </div>  	
    </div>	
</template>

<script>
    import {mapGetters} from 'vuex'
    import Star from '../star/star.vue'
    export default{
    	computed:{
    		...mapGetters([
    			    'detailshow',
    			    'seller',
    			])
    	},
        components:{
            'star':Star
        },
    	created(){
			this.$store.dispatch('getSeller');
            this.iconClassMap = ['decrease','discount','special','invoice','guarantee'];
		},
    	methods:{
    		close(){
    			this.$store.dispatch('closedetail')
    		}
    	}
    }	
</script>

<style lang='less' scoped>
    .detail{
    	z-index: 666;
    	position:fixed;
    	top:0;
    	left:0;
    	right:0;
    	bottom:0;
    	background: rgba(7,17,27,0.8);
    	padding:1.28rem 0.72rem 0.64rem 0.72rem;
    	color:rgb(255,255,255);
    	.title{
    		font-size:0.32rem;
    		font-weight:700;
    		text-align:center;
    	}
        .stars{
            text-align: center;
            margin-top:0.32rem;
               
        }
    	.discounts{
            margin-top:0.56rem;
    		display:flex;
    		.line{
    			flex:1;
    			position: relative;
    			top:.13rem;
                border-top:2px solid rgba(255,255,255,0.2);
                width:2.24rem; 
                height:0;    
    		}
    		.message{
    			margin: 0 .2rem;
                font-size:0.28rem;
    		    color:rgb(255,255,255);
    		    font-weight:700;
    		} 
    	}
        .discounts-detail{
            padding-top:0.48rem;
            padding-left:0.24rem;
            li{
            margin-bottom: 0.24rem; 
               .icon{
                display:inline-block;
                width:0.32rem;
                height:0.32rem;
                background-size: 0.32rem 0.32rem;
                &.decrease{
                    background-image: url('../../../static/imgs/decrease.png');
                    vertical-align:middle;

                }
                &.discount{
                    background-image: url('../../../static/imgs/discount.png');
                    vertical-align:middle;
                }
                &.special{
                    background-image: url('../../../static/imgs/special.png');
                    vertical-align:middle;
                }
                &.invoice{
                    background-image: url('../../../static/imgs/invoice.png');
                    vertical-align:middle;
                }
                &.guarantee{
                    background-image: url('../../../static/imgs/guarantee.png');
                    vertical-align:middle;
                }
            }
                .text{
                    vertical-align:middle;
                    display:inline-block;
                    padding-left: 0.12rem;
                    font-size:0.24rem;
                    font-weight:200;
                    line-height:0.24rem;
                }
            } 
        }
        .sell-message{
            //margin-top:0.56rem;   
        }
        .sell-detail{
            padding:0.48rem 0.24rem;

            font-size:0.24rem;
            font-weight:200;
            line-height:0.48rem;    
        } 
        .detail-close{
            position:absolute;
            bottom:0.64rem;
            left:50%;
            .el-icon-close{
                font-size:0.64rem; 
                color:rgba(255,255,255,0.5); 

            }
        }   
    }	
</style>



