<template>
	<div class="carCount">
        <div class="reduce" v-show='food.count' @click.stop='decrement'>
        	<i class="iconfont icon-jianhao"></i>
        </div>
        <div class="sum" v-show='food.count'>{{food.count}}</div>
        <div class="add" @click.stop='increment'>
        	<i class="iconfont icon-jiahao"></i>
        </div>
	</div>
</template>

<script>
    import Vue from 'vue';
    import {mapGetters} from 'vuex'	
    export default{
    	props:['food'],
    	methods:{
    		increment(){
    			if(!this.food.count){
    				Vue.set(this.food,'count',1);
    			}else{
    				this.food.count++;
    			}
    		},
    		decrement(){
    			if(this.food.count){
    				this.food.count--;
    			}
    		}
    	}
    }
</script>

<style lang='less' scoped>
    .carCount{
    	width:1.1rem;
    	height:0.3rem;
    	position:relative;
    	font-size:0.48rem;
    	.add{
            flex:1;
    		position:absolute;
    		right:0;
    		color:#1d90e6;
    		bottom:0.1rem;
    		.icon-jiahao{
    		    font-size:0.38rem;	
    		}
    	}
    	.reduce{
    		position:absolute;
    		left:0;
    		color:#1d90e6;
    		bottom:0.1rem;
    		.icon-jianhao{
    		    font-size:0.35rem;	
    		}
    	}
    	.sum{
    		position:absolute;
    		left:0.42rem;
            top:0rem;
    		font-size:0.2rem;
    		color:rgb(147,153,159);
    		line-height:0.1rem;
    	}
    }	
</style>


