<template>
	<div class="header">
        <div class="background">
        	<img :src="seller.avatar" alt="">
        </div>
	    <div class="header-top">
			<div class="left">
		    	<img :src="seller.avatar" alt="" class="avatar">
		    </div>
		    <div class="right">
		    	<img src="../../../static/imgs/brand.png" alt="" class="brand">
		    	<span class="title">{{seller.name}}</span>
		    	<p class="description">{{seller.description}}/{{seller.deliveryTime}}</p>
		    	<img src="../../../static/imgs/decrease.png" alt="" class="decrease">
		    	<span class="supports-description" v-if="seller.supports">{{seller.supports[0].description}}</span>	
		    </div>
		    <div class="knowMore" @click='show'>
		        <span class="count" v-if="seller.supports">{{seller.supports.length}}</span>
		        <i class="el-icon-arrow-right"></i>
		    </div>
	    </div>
	    <div class="bottom">
	    	<img src="../../../static/imgs/bulletin.png" alt="" class="bulletin-img">
	    	<span class="bulletin-span">{{seller.bulletin}}</span>
	    	<i class="el-icon-arrow-right"></i>
	    </div>	
	    <detail></detail>	
	</div>
</template>

<script>
	import {mapGetters} from 'vuex'
	import Detail from '../detail/Detail.vue'
	export default{
		computed:{
		   ...mapGetters([
		       'seller',
		   ])	
		},
		components:{
			Detail
		},
		created(){
			this.$store.dispatch('getSeller');
		},
		methods:{
			show(){
				this.$store.dispatch('showdetail');
			}
		}
	}
</script>

<style lang='less' scoped>
    .header{
    	height:2.2rem;
    	background:rgba(7,17,27,0.5);
    	padding-top:0.48rem;
    	padding-left:0.48rem;
    	position:relative;
    	//opacity:0.99;
    	//z-index:-1;
    	.background{
    		position:absolute;
    		top:-0.48rem;
    		left:-0.48rem;
    		bottom:-0.48rem;
    		right:-0.48rem;
    		z-index:-3;
    		filter:blur(0.1rem);
    		img{
    			width:100%;
    			height:100%;
    		}
    	}
    	.header-top{
    		background:gray;
    		position:relative;
	        .left{
	    	    float:left;
	    	    width:1.28rem;
	    		height:1.28rem;
	    	    .avatar{
	    		width:1.28rem;
	    		height:1.28rem;
	    		border-radius: 0.04rem;
	    	    }	
	    	}
	    	.right{
	    		float:left;
	    		padding-left: 0.32rem;
	    		height:1.28rem;
	    		line-height:0.6rem;
	    		.brand{
	    			margin-right: 0.12rem;
	    			width:0.6rem;
	    			height:0.36rem;
	    		}
	    		.title{
	    		    font-size:0.32rem;
	    		    color:rgb(255,255,255);
	    		    font-weight:bold;
	    		    line-height:0.36rem;
	    	    }
	    	    .description{
			        font-size: 0.24rem;
				    color: rgb(255,255,255);
				    font-weight: 2;
				    line-height: 0.24rem;		
			    }
			    .decrease{
			    	margin-right: 0.08rem;
	                width:0.32rem;
	    			height:0.32rem;
			    }
	            .supports-description{

	                font-size: 0.2rem;
				    color: rgb(255,255,255);
				    font-weight: 2;
				    line-height: 0.24rem;	
	            }
	    	}
	    	.knowMore{
	    		color:white;
	    		position:absolute;
	    	    width:0.8rem;
	    	    height:0.48rem;
	    	    line-height:0.48rem;
	    	    background-color: rgba(0,0,0,0.2); 
	    	    top:0.8rem;
	    	    right:0.24rem;
	    	    //opacity:0.4;
	    	    border-radius:30%;
	    	    text-align:center;
	    	    .count{
	    	    	color:rgb(255,255,255);
	    	    	font-size:0.2rem;
	    	    	
	    	    }	
	    	    
	    	}
	    } 
	    .bottom{
	    	color:white;
	    	height:0.56rem;
	    	width:100%;
	    	position:absolute;
	    	background-color:rgba(7,17,27,0.2);
	    	z-index:0;
	    	//opacity:0.6;
	    	bottom:0;
	    	left:0;
	    	padding-left: 0.24rem;
	    	.bulletin-img{6
                width:0.44rem;
                height:0.24rem;
                margin-top:0.15rem;

	    	}
	    	.bulletin-span{
	    		display:inline-block;
	    		padding-left: 0.08rem;
	    	   color:white;	
	    	   font-size:0.2rem;
	    	   width:81%;
	    	   overflow: hidden;
	    	   white-space: nowrap;
	    	   text-overflow:ellipsis;
               
	    	}
	    	.el-icon-arrow-right{
	    	    display:inline-block;	
	    	    margin-left: 0.08rem;
	    	}
	    }   	    	
    }	
</style>


