<template>
    <transition enter-active-class="animated rotateIn" leave-active-class="animated rotateOut">
	<div class="goodsdetail" v-show="foodshow" ref="qwe">
		<div class="detail-content" >
    			<div class="image">
    				<img :src="food.image" alt="">
    			</div>
    			<div class="back" @click='hidefood'>
    				<i class="icon iconfont icon-shiliangzhinengduixiang4"></i>
    			</div>
    			<div class="main">	
                    <h1>{{food.name}}</h1>
                    <div class="num">
                        <span>{{food.sellCount}}</span>
                        <span>{{food.rating}}%</span>
                    </div>
                    <div class="money"><span style="font-size:0.28rem;">{{food.price}}</span></div>
                    <div class="shopCart">
                        <div class="show">
                            <carcount :food='food'></carcount> 
                        </div>
                        <div class="addCar" @click="addCart($event)" v-show="!food.count"></div>    
                    </div>
                    
    			</div>
                <div class="line"></div>
                <div class="introduce">
                    <h1></h1>
                    <span>{{food.info}} </span>
                </div>
                <div class="line"></div>
                <div class="ratting">
                <div class="r-top">
                    <h1></h1>
                    <div class="block one" @click="getAll" :class="{'getAll':'getAll'===cc}"><span>{{ratings.length}}</span></div>
                    <div class="block two" @click="ok" :class="{'ok':'ok'===cc}"><span>{{countNum}}</span></div>
                    <div class="block three" @click="no" :class="{'no':'no'===cc}"><span>{{ratings.length-countNum}}</span></div>
                </div>
                <div class="r-bottom" :class="{'checked':flag}" @click="changeFlag">   
                    <i class="iconfont icon-duihao icon"></i>
                    <span class="text"></span>
                </div>
            </div>
            <div class="detail">
                <ul >
                    <li v-for="v in ratLists" v-show="all || yes === v.rateType">
                        <div class="oneDetail">
                            <div class="topDetail">
                                <div class="rateTime">{{v.rateTime | date}}</div>
                                <div class="userDetail">
                                    <span>{{v.username}}</span>
                                    <img :src="v.avatar" alt="">
                                </div>
                            </div>
                            <div class="main-rating">
                                <span v-if="v.rateType==0" class="good"><i class="iconfont icon-damuzhion"></i></span>
                                <span v-else="v.rateType==1" class="bad"><i class="iconfont icon-down"></i></span>
                                <span class="text">{{v.text}}</span>   
                            </div>
                        </div>
                    </li>
                </ul>   
            </div>  
        </div>
        <v-footer></v-footer> 
	</div>
    </transition>
</template>

<script>
    import Vue from 'vue'
    import {mapGetters} from 'vuex'
    import Footer from '../footer/footer.vue'
    import carcount from '../carCount/carCount.vue'
    export default{
        data(){
            return {
                all:true,
                yes:-1,
                flag:false,
                cc:'getAll'
            }
        },
        mounted() {
setTimeout(()=>{
     this._initDetail();
}, 20)
        },
        watch : {
            food(){
                this.$nextTick(()=>{
                    if(!this.detailWrapper ){
                        this._initDetail();
                    }else{
                        this.detailWrapper.refresh();
                    }
                });        
            }
        },
    	computed:{
    		...mapGetters([
                'foodshow',
                'food'
    			]),
            ratings() {
                console.log(this.food.ratings)
                return this.food.ratings
            },
            ratLists(){
                if(this.flag){
                    return this.ratings.filter((item)=>{
                        return item.text
                    })
                }
                return this.ratings;
            },
            countNum(){
                return this.ratings.filter((item)=>{
                    return item.rateType ===0
                }).length;
            }
    	},
        created(){
            this.$store.dispatch('getRatings');
        },
        filters:{
            date:function(value){
                function toDouble(n){
                    return n<10?'0'+n:n;
                }
                var d=new Date(value);
                return d.getFullYear()+"-"+toDouble(d.getMonth()+1)+"-"+toDouble(d.getDate())+" "+toDouble(d.getHours())+":"+toDouble(d.getMinutes());
            }
        },
        components:{
            'v-footer':Footer,
            carcount
        },
    	methods:{
            changeFlag(){
                this.flag=!this.flag
                this.refresh()
            },
    		hidefood(){
                //console.log(55);
    			this.$store.dispatch('hidefood');
    		},
            refresh(){
                setTimeout(()=>{
                    this.detailWrapper.refresh()
                },1000)
            },
            _initDetail(){
                this.detailWrapper = new BScroll(this.$refs.qwe,{click:true});
            },
            addCart(event){
                if(!this.food.count){
                    Vue.set(this.food,'count',1);
                }else{
                    this.food.count--;
                }        
            },
            ok(){
                //alert(2)
                this.yes=0
                this.all=false
                this.refresh()
                this.cc='ok'
            },
            no(){
                //alert(3)
                this.yes=1
                this.all=false
                this.refresh()
                this.cc='no'   
            },
            getAll(){
                //alert(1)
                this.yes=-1
                this.all=true
                this.refresh()
                this.cc='getAll'
            }
    	}
    }	
</script>

<style lang='less' scoped>
    .goodsdetail{
    	position:fixed;
    	top:0;
    	z-index:999;
    	left:0;
    	right:0;
    	bottom:0;
        background:white;
        overflow:hidden;
        .detail-content{
            .line{
                width:100%;
                height:0.32rem;
                background-color:#eef1f6;
            }
    		.image{
    			position:relative;
    			width:100%;
    			height:0;
    			padding-bottom:100%;
    			img{
    				position:absolute;
    				width:100%;
    				height:100%;
    				left:0;
    				top:0;
    			}
    		}
    		.back{
    			position:fixed;
    			top:0.1rem;
    			left:0.1rem;
    			width:0.1rem;
    			heigth:0.1rem;
    			color:white;
    			padding:0.2rem;
                //border: 1px solid #000;
    		}
            .main{
                padding:0.36rem;
                //border-bottom: 2px solid rgba(7,17,27,0.1);
                position:relative;
                .show{
                    position:absolute;
                    bottom:0.5rem;
                    right:0.36rem;    
                }
                .addCar{
                    width:1.48rem;
                    height:0.48rem;
                    border-radius: 0.24rem;
                    background-color:rgb(0,160,220);
                    font-size:0.2rem;
                    position:absolute;
                    bottom:0.5rem;
                    right:0.36rem;
                    text-align: center;
                    color:white;
                    line-height:0.48rem;
                }
                h1{
                    font-size:0.28rem;
                    font-weight:700;
                    color:rgb(7,17,27);
                    line-height:0.28rem;
                }
                .num{
                    font-size:0.2rem;
                    color:rgb(147,153,159);
                    line-height:0.2rem;
                    margin-top:0.16rem;
                    span{
                        padding-right:0.24rem;
                    }
                }
                .money{
                    font-size:0.2rem;
                    margin-top: 0.36rem;
                    font-weight:700;
                    color:red;
                    line-height:0.48rem;
                }
            }
            .introduce{
                padding:0.36rem;
                border-top: 2px solid rgba(7,17,27,0.1);
                border-bottom: 2px solid rgba(7,17,27,0.1);
                h1{
                    font-size:0.28rem;
                    font-weight:700;
                    color:rgb(7,17,27);
                    line-height:0.28rem;
                }
                span{
                    font-size:0.24rem;
                    font-weight:200;
                    color:rgb(77,85,93);
                    line-height:0.48rem;
                    padding:0.12rem 0.16rem 0 0.16rem;
                }
            }
            .ratting{
                padding:0.36rem;
                border-top: 2px solid rgba(7,17,27,0.1);
                border-bottom: 2px solid rgba(7,17,27,0.1);
                height:2.4rem;
                .r-top{
                    border-bottom: 1px solid rgba(7,17,27,0.1);
                    height:1.65rem;
                    h1{
                        font-size:0.28rem;
                        font-weight:700;
                        color:rgb(7,17,27);
                        line-height:0.28rem;
                        padding-bottom: 0.36rem;   
                    } 
                    .block{
                        float:left;
                        font-size:0.24rem;
                        color:rgb(255,255,255);
                        line-height:0.32rem;
                        background-color:#3399ff;
                        padding:0.16rem 0.24rem;
                        margin-right:0.16rem;
                        border-radius:0.02rem; 
                    } 
                    .one{
                        &.getAll{
                            background:rgb(0,160,220);
                        }
                    }
                    .two{
                        color:rgb(77,85,93);
                        background-color:#99ccff;
                        &.ok{
                            background-color:rgba(0,160,220,0.2);
                        }
                    }
                    .three{ 
                        background-color:#ccc;
                        color:rgb(77,85,93);
                        &.no{
                           background-color:rgba(77,85,93,0.2); 
                        }
                    } 
                }
                .r-bottom{
                    margin:0.24rem 0;
                    color:rgb(147,153,159);
                    line-height:0.48rem;
                    &.checked{
                        color:rgb(0,160,220);
                    } 
                }
            }
            .detail{
                padding:0.36rem; 
                border-bottom: 2px solid rgba(7,17,27,0.1);
                border-top: 2px solid rgba(7,17,27,0.1); 
                .oneDetail{
                    border-bottom: 1px solid rgba(7,17,27,0.1);
                    overflow: hidden;
                    padding:0.32rem 0; 
                    .topDetail{
                        width:100%;
                        height:0.5rem;
                        .rateTime{
                            float:left;
                            font-size:0.24rem;
                            color:rgb(147,153,159);
                            line-height:0.32rem;

                        }
                        .userDetail{
                            float:right;
                            font-size:0.2rem;
                            color:rgb(147,153,159);
                            line-height:0.24rem;
                            img{
                                width:0.24rem;
                                height:0.24rem;
                                border-radius: 0.12rem;
                                margin-left: 0.12rem;
                            }
                        }    
                    }
                    .main-rating{
                        width:100%;
                        clear:both;
                        .good{    
                            color:rgb(0,160,220);   
                        }
                        .bad{
                            color:rgb(183,187,191);        
                        }
                        .text{
                            font-size:0.24rem;
                            color:rgb(7,17,27);
                            line-height:0.32rem;
                            padding-left: 0.08rem;   
                       }
                    }
                }  
            }
    	}
    }	
</style>


