<template>
	<div class='seller' ref="sellerWrapper">
    <div>
    <div class="one">
        <div class="first">
           <h1 class="title">{{seller.name}}</h1>
            <div class="stars">
                <star :score="seller.score" :size="24"></star>
                <span class="rate-count">({{seller.ratingCount}})</span>
                <span class="sell-count">{{seller.sellCount}}</span>    
            </div>
            <div class="heart" @click="change"> 
                <i class="iconfont icon-xin" :class="{'yes':aaa}"></i>
                <p v-show="!aaa"></p>
                <p v-show="aaa"></p>
            </div> 
        </div>
        <div class="remark">
            <div class="block" style="border-right:1px solid rgba(7,17,27,0.1);">
                <h2></h2>
                <div class="content">
                    <span class="num" style="font-size:0.4rem;">{{seller.minPrice}}</span>
                </div>
            </div>
            <div class="block" style="border-right:1px solid rgba(7,17,27,0.1);">
                <h2></h2>
                <div class="content">
                    <span class="num" style="font-size:0.4rem;">{{seller.deliveryPrice}}</span>
                </div>
            </div>
            <div class="block">
                <h2></h2>
                <div class="content">
                    <span class="num" style="font-size:0.4rem;">{{seller.deliveryTime}}</span>
                </div>
            </div>
        </div>
    </div>
    <div class="back"></div>
    <div class="two">
        <div class="tell">
            <h1></h1>
            <p class="bulletin">{{seller.bulletin}}</p>
        </div>
        <div class="discounts-detail">
            <ul v-if="seller.supports" class="supports">
                <li class="support-item" v-for="item in seller.supports">
                    <span class="icon" :class="iconClassMap[item.type]"></span>
                    <span class="text">{{item.description}}</span>
                    <div class="line"></div>
                </li>
            </ul>   
        </div>
    </div>
    <div class="back"></div>
    <div class="three">
        <h1></h1>
        <div class="img-wrapper" ref="picWrapper">
            <ul>
                <li v-for="pic in seller.pics">
                    <img  :src="pic" >
                </li>
            </ul>
        </div>
    </div>
    <div class="back"></div>
    <div class="four">
        <h1></h1>
        <div class="sell-message">
            <ul>
                <li v-for="v in seller.infos">
                    {{v}}
                </li>
            </ul>
        </div>

    </div>
    </div>
    <v-footer></v-footer>    
    </div>	
</template>

<script>
    import {mapGetters} from 'vuex'
    import Star from '../star/star.vue'
    import Footer from '../footer/footer.vue'
    export default{
    data(){
        return{
            aaa:false
        }
    },
      created(){
        this.$store.dispatch('getSeller');
        this.iconClassMap = ['decrease','discount','special','invoice','guarantee'];
        this.$nextTick(() => {
            setTimeout(() => {
                this._initSeller();
                this._initPicScroll();
            },100)
        }) 
      },
      computed:{
        ...mapGetters([
            'seller'
            ]),

      },
      components:{
            'star':Star,
            'v-footer':Footer
        },
      methods:{
        change(){
            //alert(1);
            this.aaa=!this.aaa;
        },
        refresh(){
                setTimeout(()=>{
                    this.sellerBS.refresh()
                    this.picsBS.refresh()
                },100)
            },
        _initSeller(){
            this.sellerBS = new BScroll(this.$refs.sellerWrapper,{
                click:true
            });
        },
        _initPicScroll(){
            this.picsBS = new BScroll(this.$refs.picWrapper,{
                scrollX:true,
                click:true
            });
        }
      }
      	
    }	
</script>

<style lang='less' scoped>
	.seller{
    	position:absolute;
    	top:3.48rem;
    	bottom:0.92rem;
    	width:100%;
        overflow: hidden;
        .back{
            width:100%;
            height:0.32rem;
            background-color:#eef1f6;
        }
        .one{
            width:90%;
            padding:0.36rem;
            border-bottom: 2px solid rgba(7,17,27,0.1); 
            .first{
            width:100%;
            height:1.1rem;
            border-bottom: 1px solid rgba(7,17,27,0.1);
             position:relative;
             padding-bottom:0.36rem; 
                .heart{
                    position:absolute;
                    right:0;
                    top:0;
            
                    .icon-xin{
                        display:block;
                        font-size: 0.48rem;
                        color:rgb(77,85,93);
                        padding-bottom: 0.08rem;
                        &.yes{
                        color:red;
                        }
                    }
                    p{
                        font-size:0.2rem;
                        color:rgb(77,85,93);
                        line-height:0.2rem; 
                    }
                }
               .title{
                    font-size:0.28rem;
                    color:rgb(7,17,27);
                    line-height:0.28rem;   
                }
                .stars{
                margin-top:0.32rem;
                float:left;
                .star{
                    display:inline-block;   
                }
                .rate-count{
                    font-size:0.2rem;
                    color:rgb(77,85,93);
                    line-height:0.36rem;
                    height:0.36rem;
                    display:inline-block;
                    padding-right:0.24rem;
                }
                .sell-count{
                    font-size:0.2rem;
                    color:rgb(77,85,93);
                    line-height:0.36rem;
                    height:0.36rem;
                }
                clear:both;
            } 
        }
        
        .remark{
            margin-top:0.36rem;
            display:flex;
            .block{
                text-align: center;
                flex:1;
                float:left;
                
                h2{
                    font-size:0.2rem;
                    color:#999;
                    line-height:0.2rem;
                    margin-bottom:0.08rem; 
                }
                .content{
                    span{
                        font-size:0.2rem;
                        font-weight:200;
                        color:rgb(7,17,27);
                        line-height:0.48rem;
                    }
                }
            }
        }
        }
        .two{
            padding:0.36rem;
            border-top:2px solid rgba(7,17,27,0.1); 
            border-bottom:2px solid rgba(7,17,27,0.1);  
            .tell{
                padding-bottom: 0.32rem;
                border-bottom: 1px solid rgba(7,17,27,0.1);
                h1{
                    font-size:0.28rem;
                    color:rgb(7,17,27);
                    line-height:0.28rem;
                    margin-bottom: 0.16rem;   
                }
                .bulletin{
                    font-size:0.24rem;
                    font-weight:200;
                    color:rgb(240,20,20);
                    line-height:0.48rem;
                    padding:0 0.24rem;
                }
            }
            .discounts-detail{
            padding-top:0.48rem;
            //padding-left:0.24rem;
            li{
               margin-bottom: 0.24rem;
               .line{
                margin-top: 0.24rem;
                width:100%;
                border-top:1px solid rgba(7,17,27,0.1); 
                } 
               .icon{
                display:inline-block;
                width:0.32rem;
                height:0.32rem;
                background-size: 0.32rem 0.32rem;
                &.decrease{
                    background-image: url('../../../static/imgs/decrease.png');
                    vertical-align:middle;

                }
                &.discount{
                    background-image: url('../../../static/imgs/discount.png');
                    vertical-align:middle;
                }
                &.special{
                    background-image: url('../../../static/imgs/special.png');
                    vertical-align:middle;
                }
                &.invoice{
                    background-image: url('../../../static/imgs/invoice.png');
                    vertical-align:middle;
                }
                &.guarantee{
                    background-image: url('../../../static/imgs/guarantee.png');
                    vertical-align:middle;
                }
            }
            
            .text{
                    vertical-align:middle;
                    display:inline-block;
                    padding-left: 0.12rem;
                    font-size:0.24rem;
                    font-weight:200;
                    line-height:0.32rem;
                    color:rgb(7,17,27);
                }
            }
            }
        }
        .three{
            padding:0.36rem 0 0.36rem 0.36rem;
            width:100%;
            border-top:2px solid rgba(7,17,27,0.1); 
            border-bottom:2px solid rgba(7,17,27,0.1);  
            h1{
                font-size:0.28rem;
                color:rgb(7,17,27);
                line-height:0.28rem;
                margin-bottom: 0.16rem;   
            }
            .img-wrapper{
                width:100%;
                overflow: hidden;
                white-space:nowrap;
                ul{
                    width:11rem;
                    li{
                        float:left;
                        display:inline-block;
                        img{
                           width:2.4rem;
                           height:1.8rem;
                           margin-right:0.12rem;   
                        }      
                    } 
                }
                
            }    
        }
        .four{
            padding:0.36rem;
            h1{
                font-size:0.28rem;
                color:rgb(7,17,27);
                line-height:0.28rem;
                margin-bottom: 0.24rem;    
            }
            ul{
                height:3.8rem;
                li{
                    padding:0.32rem 0.24rem;
                    font-size:0.24rem;
                    font-weight:200;
                    color:rgb(7,17,27);
                    line-height: 0.32rem;
                    border-top: 1px solid rgba(7,17,27,0.1);
                } 
            }
            
        }
        
    }	
</style>


