<template>
    <div class="footer">
        <div class="outcircle" style="color:white;">
        	<div @click="listToggle" class="inCircle" :class='{"yes":totalCount>0}'>
        		<i class="iconfont icon-gouwuche"></i>
        	</div>
            <div class="num" v-show="totalCount">{{totalCount}}</div>
        </div>
        <div class="footerLeft">{{totalPrice}}</div>
        <div class="footerMid">{{seller.deliveryPrice}}</div>
        <div class="footerRight">
            <div class="pay" :class='{"enough":totalPrice>=seller.minPrice}'>{{payDesc}}</div>
        </div>
        <!-- <transition name="transHeight"> -->
        <div class="shopcart-list" v-show="listShow">
            <div class="list-header">
                <div class="title"></div>
                <div class="empty" @click="setEmpty"></div>
            </div>
            <div class="list-content" ref="foodlist">
                <div>
                <ul>
                    <li class="food" v-for="food in selectFoods">
                        <span class="name">{{food.name}}</span>
                        <div class="price">
                            <div class="detail"><span style="font-size:0.28rem;font-weight:700;">{{food.price*food.count}}</span></div>
                        
                            <div class="carCount-wrapper">
                                <carcount :food="food"></carcount>   
                            </div>
                        </div>
                    </li>
                </ul>
                </div>   
            </div>
        </div>   
        <!-- </transition> -->
        <!-- <transition name="fade-backdrop"> -->
        <div class="backdrop" v-show="showBackdrop" @click="hideBackdrop"></div>
    <!-- </transition> -->  
    </div>
    	
</template>

<script>
    import {mapGetters} from 'vuex'
    import carcount from '../carCount/carCount.vue'
    export default{
        data(){
            return {
                listShow:false ,
                foods:[]   
            }
        },
        computed:{
            ...mapGetters([
                'seller',
                'goods'
                ]),
            showBackdrop(){
                if(this.listShow && this.totalPrice){
                    return true
                } 
                this.listShow = false
                return false 
            },
            selectFoods(){
                let foods = [];
                this.goods.forEach(good =>{
                    good.foods.forEach(food=>{
                        if(food.count>0){
                            foods.push(food);
                        }
                    })
                })
                return foods;
            },
            totalPrice(){
                let total=0;
                this.selectFoods.forEach(food=>{
                    total+=food.price*food.count;
                });
                return total;
            },
            totalCount(){
                let count=0;
                this.selectFoods.forEach(food=>{
                    count+=food.count;
                });
                return count;  
            },
            payDesc(){
                if(this.totalPrice===0){
                    return `${this.seller.minPrice}`
                }else if(this.totalPrice<this.seller.minPrice){
                    return `${this.seller.minPrice-this.totalPrice}`
                }else{
                    return ""
                }
            }
        },
        methods:{
            _initScroll(){
                this.foodlistScroll=new BScroll(this.$refs.foodlist,{
                    click:true
                })
            },
            listToggle(){
                //console.log('45');
                if(!this.totalCount){
                    return
                }
               // alert(1);
                this.listShow=!this.listShow;

                if(this.listShow){
                    setTimeout(()=>{
                        if(!this.foodlistScroll){
                            this._initScroll();
                        }else{
                            this.foodlistScroll.refresh();
                        }
                     
                },1000)
                    
                }  
            },
            setEmpty(){
                this.listShow=false; 
                this.selectFoods.forEach((food)=>{
                    food.count=0
                })
            },
            hideBackdrop(){
                this.listShow = false;
            }
        },
        components:{
            carcount
        }
    }	
</script>

<style lang='less' scoped>	
    .footer{
    	width:100%;
    	height:0.92rem;
    	background-color:#1d1427;
    	position:fixed;
    	left:0;
    	bottom:0;
    	.outcircle{
    		width:1.12rem;
    		height:1.12rem;
    		background-color:#1d1427;
            position:absolute;
            left:0.36rem;
            bottom:0.16rem;
            border-radius:0.56rem; 
            .inCircle{
            	width:0.88rem;
            	height:0.88rem;
            	border-radius: 0.44rem;
            	position:absolute;
            	left:0.12rem;
            	bottom:0.12rem;
            	background-color: rgba(255,255,255,0.2);
            	color:rgba(255,255,255,0.4);
            	font-size:0.28rem;
            	text-align:center;
            	line-height:0.88rem;
                &.yes{
                    background:rgb(0,160,220);
                    color:white;
                }
            }
            .num{
                width:0.48rem;
                height:0.34rem;
                background-color:red; 
                border-radius: 0.12rem;
                position:absolute;
                right:0; 
                text-align: center;
                line-height:0.34rem; 
            }
    	}
    	.footerLeft{
			// widt:0.4rem;
			font-size: 0.32rem;
			color:rgba(255,255,255,0.4);
			margin-left: 1.6rem;
			margin-top: 0.24rem;
			margin-bottom: 0.24rem;
			line-height: 0.48rem;
			padding-right: 0.24rem;
			border-right: 0.02rem solid rgba(255,255,255,0.1);
			float:left;
		}
    	.footerMid{
			font-size: 0.24rem;
			color:rgba(255,255,255,0.4);
			margin-left: 0.24rem;
			margin-top: 0.24rem;
			margin-bottom: 0.24rem;
			line-height: 0.52rem;
			float:left;
		}
		.footerRight{
			width:2.1rem;
			height: 1.1rem;
			float:right;
			background-color: rgba(255,255,255,0.2);
			color:rgba(255,255,255,0.4);
			font-size: 0.32rem;
			text-align: center;
			line-height: 1.1rem; 
            .pay{
                &.not-enough{
                    background-color: #2b333b;
                }
                &.enough{
                    background: #00b43c;
                    color: #fff;    
                }
            }
        }
        .shopcart-list{
            position:absolute;
            left:0;
            
            bottom: 0.92rem;
            background: white;
            //color: #fff;
            width:100%;
            z-index: -1;
            
            .list-header{
                border-bottom: 2px solid rgba(7,17,27,0.1);
                height:0.8rem;
                background-color: #f3f5f7;
                .title{
                    float:left;
                    font-size: 0.28rem;
                    font-weight:200;
                    color:rgb(7,17,27);
                    line-height: 0.8rem;
                    padding-left:0.36rem;
                }
                .empty{
                    float:right;
                    font-size: 0.24rem;
                    color:rgb(0,160,220);
                    line-height: 0.8rem;
                    padding-right:0.36rem;
                }
            }
            .list-content{
                padding:0 0.36rem;
                overflow: hidden;
                max-height:4rem;
                .food{
                    height:0.96rem;
                    line-height:0.96rem;
                    font-size:0.28rem;
                    border-bottom: 1px solid rgba(7,17,27,0.1);
                }
                .price{
                    position:relative;
                    .detail{
                        position:absolute;
                        right:1.6rem;
                        top:0.05rem;
                        font-size: 0.2rem;
                        color:rgb(240,20,20);


                    }
                    .carCount-wrapper{
                        position:absolute;
                        right:.2rem;
                        top:0.45rem;
                        line-height: 0.32rem;
                    }
                    float:right;
                }
                

            }
        } 
        .backdrop{
        position:fixed;
        top:0;
        bottom:0;
        left:0;
        right:0;
        background:rgba(7,17,27,0.6);
        filter:blur(0.1rem);
        z-index: -2;
    }  
    }
    
</style>


