<template>
	<div class='ratings' ref="ratingsWrapper">
    	<div class="rating-content">
            <div class="first">
                <div class="left">
                    <h1>{{seller.score}}</h1>
                    <p></p>
                    <span>{{seller.rankRate}}%</span>
                </div>
                <div class="right">
                    <div class="stars">
                        <span class="left-pan"></span>
                        <star :score="seller.serviceScore" :size="24"></star>
                        <span class="right-pan">{{seller.serviceScore}}</span>
                    </div>
                    <div class="stars">
                        <span class="left-pan"></span>
                        <star :score="seller.foodScore" :size="24"></star>
                        <span class="right-pan">{{seller.foodScore}}</span>
                    </div> 
                    <div class="time">
                        <span></span>
                        <span style="color:rgb(147,153,159);">{{seller.deliveryTime}}</span>
                    </div>  
                </div>
            </div>
            <div class="line"></div>
            <div class="ratting">
                <div class="r-top">
                    <h1></h1>
                    <div class="block one" @click="getAll" :class="{'getAll':'getAll'===cc}"><span>{{ratings.length}}</span></div>
                    <div class="block two" @click="ok" :class="{'ok':'ok'===cc}"><span>{{countNum}}</span></div>
                    <div class="block three" @click="no" :class="{'no':'no'===cc}"><span>{{ratings.length-countNum}}</span></div>
                </div>
                <div class="r-bottom" :class="{'checked':flag}" @click="changeFlag">   
                    <i class="iconfont icon-duihao icon"></i>
                    <span class="text"></span>
                </div>
            </div>
            <div class="line"></div>
            <div class="detail">
                <ul >
                    <li v-for="v in ratLists" v-show="all || yes === v.rateType">
                        <div class="oneDetail">
                            <div class="left">
                                <img :src="v.avatar" alt="">
                            </div>
                            <div class="right">
                                <h1>{{v.username}}</h1>
                                <div class="rateTime">
                                    {{v.rateTime | date}}   
                                </div>
                                <div class="stars">
                                    <star :score="seller.serviceScore" :size="12"></star>
                                    <span class="right-pan">{{seller.deliveryTime}}</span>
                                </div>
                                <p>{{v.text}}</p>
                                <div class="satisfy">
                                    <span v-if="v.rateType==0" class="good"><i class="iconfont icon-damuzhion"></i></span>
                                    <span v-else="v.rateType==1" class="bad"><i class="iconfont icon-down"></i></span>
                                        <div v-for="(v,k) in v.recommend" class="foods" v-if="k<3">
                                            <span class="d-food">{{v}}</span>
                                        </div>
                                </div>
                            </div>
                        </div>
                    </li>
                </ul>   
            </div>   
        </div>   
        <v-footer></v-footer>   
    </div>	
</template>

<script>
    import {mapGetters} from 'vuex'
    import Star from '../star/star.vue'
    import Footer from '../footer/footer.vue'
    export default{
    data(){
        return {
            all:true,
            yes:-1,
            flag:false,
            cc:'getAll'
        }
    },
    computed:{
        ...mapGetters([
            'seller',
            'ratings',
            ]),
        ratLists(){
            if(this.flag){
                return this.ratings.filter((item)=>{
                    return item.text
                })
            }
            return this.ratings;
        },
        countNum(){
            return this.ratings.filter((item)=>{
                return item.rateType ===0
            }).length;
        }
      },
    created(){
        this.$store.dispatch('getRatings');
    },
    filters:{
        date:function(value){
            function toDouble(n){
                return n<10?'0'+n:n;
            }
            var d=new Date(value);
            return d.getFullYear()+"-"+toDouble(d.getMonth()+1)+"-"+toDouble(d.getDate())+" "+toDouble(d.getHours())+":"+toDouble(d.getMinutes());
        }
    },
    watch:{
        ratings(){
            this.$nextTick(()=>{
                this._initRatings();
            })
        }
    },
    components:{
        'star':Star,
        'v-footer':Footer,
    },
    methods:{
        changeFlag(){
            this.flag=!this.flag
            this.refresh()
        },
        refresh(){
            setTimeout(()=>{
                this.ratingsWrapper.refresh()
            },100)
        },
        _initRatings(){
            this.ratingsWrapper = new BScroll(this.$refs.ratingsWrapper,{click:true});
        },
        ok(){
            //alert(2)
            this.yes=0
            this.all=false
            this.refresh()
            this.cc='ok'
        },
        no(){
           // alert(3)
            this.yes=1
            this.all=false
            this.refresh()
            this.cc='no'   
        },
        getAll(){
            //alert(1)
            this.yes=-1
            this.all=true
            this.refresh()
            this.cc='getAll'
        }

    }
    }	
</script>

<style lang='less' scoped>
    .ratings{
    	position:absolute;
    	top:3.48rem;
    	bottom:0.92rem;
    	width:100%;
        overflow: hidden;
    	
        .rating-content{
            .line{
                width:100%;
                height:0.32rem;
                //background-color:red;
                background-color:#eef1f6;
            }
            .first{
                padding:0.36rem; 
                border-bottom: 2px solid rgba(7,17,27,0.1);
                border-top: 2px solid rgba(7,17,27,0.1);
                height:1.6rem; 
                .left{
                    width:35%;
                    border-right: 1px solid rgba(7,17,27,0.1);
                    text-align:center;
                    float:left;
                    height:1.5rem;
                    h1{
                        font-size:0.48rem;
                        color:rgb(255,153,0);
                        line-height:0.56rem;
                        margin-bottom: 0.12rem;
                    }
                    p{
                        font-size:0.24rem;
                        color:rgb(7,17,27);
                        line-height:0.24rem;
                        margin-bottom: 0.16rem;
                    }
                    span{
                        font-size:0.2rem;
                        color:gray;
                    }
                }
                .right{ 
                    width:62%;
                    float:right;
                    //padding:0 0.48rem;
                    .stars{
                        vertical-align: center;
                        .left-pan{
                            font-size:0.24rem;
                            padding-right:0.24rem;
                            display: inline-block;
                            margin-top: -0.13rem;
                        }
                        .right-pan{
                            font-size:0.24rem;
                            padding-left:0.24rem;
                            color:rgb(255,153,0);
                        }
                        float:left;
                        .star{
                            display:inline-block;   
                            }
                    }
                    .time{
                        float:left;
                        margin-top:0.16rem;
                        font-size:0.24rem;
                        span{
                            padding-right:0.24rem;
                        }
                    }
                }
            }
            .ratting{
                padding:0.36rem;
                border-top: 2px solid rgba(7,17,27,0.1);
                border-bottom: 2px solid rgba(7,17,27,0.1);
                height:2.4rem;
                .r-top{
                    border-bottom: 1px solid rgba(7,17,27,0.1);
                    height:1.65rem;
                    h1{
                        font-size:0.28rem;
                        font-weight:700;
                        color:rgb(7,17,27);
                        line-height:0.28rem;
                        padding-bottom: 0.36rem;   
                    } 
                    .block{
                        float:left;
                        font-size:0.24rem;
                        color:rgb(255,255,255);
                        line-height:0.32rem;
                        background-color:#3399ff;
                        padding:0.16rem 0.24rem;
                        margin-right:0.16rem;
                        border-radius:0.02rem; 
                    } 
                    .one{
                        &.getAll{
                            background:rgb(0,160,220);
                        }
                    }
                    .two{
                        color:rgb(77,85,93);
                        background-color:#99ccff;
                        &.ok{
                            background-color:rgba(0,160,220,0.2);
                        }
                    }
                    .three{ 
                        background-color:#ccc;
                        color:rgb(77,85,93);
                        &.no{
                           background-color:rgba(77,85,93,0.2); 
                        }
                    } 
                }
                .r-bottom{
                    margin:0.24rem 0;
                    color:rgb(147,153,159);
                    line-height:0.48rem;
                    &.checked{
                        color:rgb(0,160,220);
                    } 
                }
            }
            .detail{
                padding:0 0.36rem; 
                border-bottom: 2px solid rgba(7,17,27,0.1);
                border-top: 2px solid rgba(7,17,27,0.1); 
                .oneDetail{
                    border-bottom: 1px solid rgba(7,17,27,0.1);
                    overflow: hidden;
                    padding:0.36rem 0; 
                    .left{
                        width:0.56rem;
                        height:0.56rem;
                        float:left;
                        img{
                            width:0.56rem;
                            height:0.56rem;
                            border-radius: 0.28rem;
                        }      
                    }  
                    .right{
                        float:left;
                        width:88%;
                        margin-left: 0.24rem;
                        position:relative;
                        h1{
                            font-size:0.2rem;
                            color:rgb(7,17,27);
                            line-height:0.24rem;
                        }
                        .rateTime{
                            position:absolute;
                            top:0;
                            right:0;  
                            font-size:0.2rem;
                            font-weight:200;
                            color:rgb(147,153,159);
                            line-height:0.24rem;  
                        }
                        .stars{
                            span{
                                position:absolute;
                                top:0.45rem;
                                left:1.5rem;
                                font-size:0.2rem;
                                font-weight:200;
                                color:rgb(147,153,159);
                            }
                        }
                        p{
                            margin-top:0.12rem;
                            line-height:0.36rem;
                            font-size:0.24rem;
                            color:rgb(7,17,27);
                        }
                        .satisfy{
                            margin-top:0.16rem; 
                            span{
                                display:inline-block;
                                float:left;
                                font-size:0.24rem; 
                                line-height:0.32rem;
                                margin-right:0.16rem;
                            }
                            .good{    
                                color:rgb(0,160,220);   
                            }
                            .bad{
                               color:rgb(183,187,191);        
                            }
                            .foods{
                                float:left;
                                .d-food{
                                    border:1px solid rgba(7,17,27,0.1);
                                    font-size:0.18rem;
                                    color:rgb(147,153,159);
                                    padding:0.05rem 0.12rem;
                                }
                                
                            }  
                        }
                    } 
                }  
            }

        }
    }	
</style>


